.test-card-person {
    background: #FAFAFA;
    border-radius: 20px;
    padding: 24px 32px;
    max-width: 210px;
    width: 100%;

    display: flex;
    flex-direction: column;

    &_control{
        max-width: none;
        width: 90%;
        justify-content: space-between;
        flex-direction: row;
        margin-top: 24px;
    }

    &__title {
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 19px;
        color: #000000;
        text-align: center;
    }

    &__btn{
        max-width: 48px;
        margin: 24px auto 0;
    }
    &__raitng{
        text-align: center;
        margin-top: 12px;
    }
}